<template>
  <div class="wrapper">
    <v-chart :options="lineoption" :key="key" autoresize></v-chart>
  </div>
</template>

<script>
import Echarts from 'vue-echarts'
import 'echarts/lib/chart/line'
import 'echarts/lib/component/dataZoom'
import 'echarts/lib/component/markLine'
import 'echarts/lib/component/tooltip'
import 'echarts/lib/component/axis'
import 'echarts/lib/component/axisPointer'
import 'echarts/lib/component/legend'
import 'echarts/lib/component/grid'
import 'echarts/lib/component/title'

export default {
  name: 'lineChart',
  components: {
    'v-chart': Echarts,
  },
  props: {
    options: {
      type: Object,
    },
  },
  data() {
    return {
      lineoption: this.options,
      key:1
    }
  },
  watch: {
    options: {
      handler(newVal) {
        this.lineoption = newVal
        // key changing key值变化使组件不复用，解决echarts数据不更新问题
        this.key+=1
      },
      deep: true,
    },
  },
}
</script>

<style scoped>
.wrapper {
  border-radius: 5px;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.3);
  margin-top: 10px;
  width: 100%;
  height: 308px;
}
.echarts {
  padding-top: 8px;
  width: 100%;
  height: 100%;
}
</style>